import React, {Component} from 'react';
import { Link } from 'react-router';
import {Icon} from "antd"
import { connect } from 'react-redux';
import { Badge } from 'antd';

class Drawer extends Component {
  renderAdmin() {
    if(this.props.isAdmin){
      return (
        <Link to="/dashboard/admin" activeClassName="active">
          <div className="item">
            <span className="icon"><Icon name="setting"/></span>
            <span className="title">Admin</span>
          </div>
        </Link>
      )
    }else{
      return <span></span>
    }
  }
  render() {
    return (
      <div className="drawer-component">
      <span id="drawer-handle">
        <i className="fa fa-caret-right" aria-hidden="true"></i>
        <i className="fa fa-caret-left" aria-hidden="true"></i>
      </span>
          <div className="item" id="header">
            <Link to="/">
              <span>YEAH
              <br/>
              {!this.props.isAdmin && <small> student</small>}
              {this.props.isAdmin && <small> admin</small>}
              </span>
            </Link>
          </div>
        <Link to="/dashboard/start" activeClassName="active">
          <div className="item">
            <Icon type="home" />
            <span className="title">
               &nbsp;Profile
            </span>
          </div>
        </Link>
        
        
        <div className="bottom">
          <Link to="/dashboard/admin" activeClassName="active">
            <div className="item">
            <Icon type="code" />
              <span className="title">
                &nbsp;Admin
              </span>
            </div>
          </Link>

          <Link to="/dashboard/upgrade" activeClassName="active">
            <div className="item">
              <Icon type="unlock" />
              <span className="title">
                &nbsp;Upgrades
              </span>
            </div>
          </Link>

          <Link to="/dashboard/setting" activeClassName="active">
            <div className="item">
              <Icon type="setting" />
              <span className="title">
                &nbsp;Settings
              </span>
            </div>
          </Link>

          <Link to="/auth/signout" activeClassName="active">
            <div className="item">
              <Icon type="logout" />
              <span className="title">
                &nbsp;Logout
              </span>
            </div>
          </Link>
        </div>
      </div>
    )
  }
}
function mapStateToProps({auth}){
  return {
    isAdmin: auth.isAdmin
  }
}

export default connect(mapStateToProps)(Drawer);

